@import url('https://fonts.googleapis.com/css?family=Lora:400,400i,700,700i');

@font-face {
  font-family: Impact;
  src: url('./fonts/impact.ttf');
}

html, body {
  height: 100%;
  font-family: 'Lora', serif;
  background: rgb(65, 65, 65);
  color: rgb(0, 255, 85);
  text-align: center;
 
}

.btn-primary {
  background: #666 !important;
  border: none !important;
  outline: none !important;
  width: 100%;
}

.form-control:focus {
  border-color: #666;
  padding-top: 5%;
  padding-bottom: 5%;
}

.btn-primary:hover, .btn-primary:active, .btn-primary:focus {
  background: #666 !important;
  outline: none !important;
  border: none !important;
}

.navbar-brand {
  display: block;
  text-align: center;
  color: #FFF;
  margin: 0px 0px 20px 0px;
  padding-bottom: 15px;
  border-bottom: 1px solid #fff;
}

.navbar-brand:focus, .navbar-brand:hover, .navbar-brand:active {
  color: #FFF;
}

.meme-gen-modal {
  max-width: fit-content;
}

.modal-body {
  display: inline-block;
  vertical-align: middle;
  border-radius: 0px;
  
  background-color: rgb(255, 255, 255);
  padding-bottom: 2%;

  
}

.modal-header {
  background-color: rgb(255, 255, 255);
  height: 5%;
  color: Black;
  

}

.meme-form {
  margin-left: 20px;
  padding-left: 20px;
  width: 512px;
  
}

.form-control:focus {
  box-shadow: none;
}

.image-holder {
  width: 200px;
  height: 200px;
  margin: 5px 5px;
  position: relative;
  box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.1);
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  border: 2px solid rgb(0, 0, 0);
  
  
}

.image-holder:hover {
  box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.3);
}

.row {
  margin: 0 auto;
  justify-content: center;
}

.main-content {
  display: flex;
}

.fixed-footer{
  bottom: 0;
  height: 100px;
}    
.header {
  display: flex;
  flex-wrap: wrap;
  margin-top: 10px;
  margin-left: 5%;
  margin-right: 5%;
  vertical-align: middle;
  width:95%;
  height:5%;
  display: inline-block;
  vertical-align: middle;
  
}



.content {
  display: flex;
  flex-wrap: wrap;
  margin-top: 2%;
  margin-left: 3%;
  margin-right: 1%;
  width: 100%;

}

.Footer {
  height: 20px;
  width: 100%;
  position: absolute;
  bottom: 0;
}

.meme-bottom-caption {
  position: absolute;
  font-size: 20px;
  display: block;
  width: 100%;
  font-family: Impact;
  text-align: center;
  text-transform: uppercase;
  bottom: 0;
  padding: 5px;
  color: #fff;
  letter-spacing: 1px;
  background: linear-gradient(to left, rgb(51, 51, 51), rgba(51, 51, 51, 0.81));
}

.meme-top-caption {
  position: absolute;
  font-size: 20px;
  display: block;
  width: 100%;
  font-family: Impact;
  text-align: center;
  text-transform: uppercase;
  top: 0;
  padding: 5px;
  color: #fff;
  letter-spacing: 1px;
  background: linear-gradient(to left, rgb(51, 51, 51), rgba(51, 51, 51, 0.81));
}
